import {
	ChangeDetectionStrategy,
	Component,
	Input,
	OnChanges,
	SimpleChanges,
} from '@angular/core';

import { NavItem } from '../../models';

type NavMenuTheme = 'dark' | 'light';

/**
 * Main navigation component
 */
@Component({
	selector: 'ui-ng-nav-menu',
	templateUrl: './nav-menu.component.html',
	styleUrls: ['./nav-menu.component.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush,
})
export class NavMenuComponent implements OnChanges {
	/**
	 * Nav Munu Items
	 */
	@Input()
	items: NavItem[] = [];

	/**
	 * Nav Menu Theme
	 */
	@Input()
	theme: NavMenuTheme = 'dark';

	/**
	 * Defines whether custom or default scrollbar should be used
	 */
	@Input()
	customScrollbar = true;

	/**
	 * Nav Menu Mini Mode Enabled (if shown only)
	 */
	@Input()
	miniMode = true;

	/**
	 * Defines menu position - fixed or default
	 */
	@Input()
	isFixed = false;
	/**
	 * Define is collapse (mini mode) triggered on
	 */
	@Input()
	isMiniOnMouseLeave = false;

	/**
	 * Search text to filter nav item by name
	 */
	@Input()
	filterValue = '';

	/**
	 * Opened menu item index;
	 */
	openedId: number | null = null;
	/**
	 * Defines if all menu items are expanded at a time (if filter is applied)
	 */
	expandAll = false;

	/**
	 * Defines if nav menu should be shown
	 */
	isShown = true;

	/**
	 * Resets openedId if items change
	 */
	ngOnChanges(changes: SimpleChanges) {
		if (changes.items) {
			this.openedId = null;
		}

		if (changes.filterValue) {
			this.expandAll = !!changes.filterValue.currentValue.trim();
		}
	}

	/**
	 * Shows nav menu
	 */
	show(): void {
		this.isShown = true;
	}

	/**
	 * Hides nav menu
	 */
	hide(): void {
		this.isShown = false;
	}

	/**
	 * Toggles nav menu appearance
	 */
	toggle(): void {
		this.isShown = !this.isShown;
	}

	/**
	 * Sets index of opened menu item
	 */
	setOpenedId(index: number | null): void {
		this.openedId = index;
	}
}
