import {
	Component,
	EventEmitter,
	Input,
	OnChanges,
	Output,
	SimpleChanges,
} from '@angular/core';

import { NavItem } from '../../models';

@Component({
	selector: 'ui-ng-nav-menu-item',
	templateUrl: './nav-menu-item.component.html',
	styleUrls: ['./nav-menu-item.component.scss'],
})
export class NavMenuItemComponent implements OnChanges {
	/**
	 * Single main many item with or without children
	 */
	@Input()
	item!: NavItem;

	/**
	 * Top-level menu item id
	 */
	@Input()
	id!: number;

	/**
	 * Opened top-level menu item id
	 */
	@Input()
	openedId!: number | null;

	/**
	 * Search text to filter nav item by name
	 */
	@Input()
	filterValue!: string;

	/**
	 * Defines if all menu items are expanded at a time (if filter is applied)
	 */
	@Input()
	expandAll!: boolean;

	/**
	 * Sets top-level menu item id
	 */
	@Output()
	setOpenId: EventEmitter<number | null> = new EventEmitter<number | null>();

	/**
	 * Marks open menu items
	 */
	isOpen = false;

	/**
	 * Updates menu item open state based on its id / owing of active item
	 */
	ngOnChanges(changes: SimpleChanges) {
		if (changes.item) {
			this.isOpen = changes.item.currentValue.children?.length
				? this.hasActiveChild(changes.item.currentValue)
				: false;
		} else if (changes.openedId && this.id) {
			this.isOpen = this.id === changes.openedId.currentValue;
		}
	}

	/**
	 * Toggles nav menu item state - open/close
	 */
	toggle(): void {
		this.isOpen = !this.isOpen;

		if (this.id) {
			this.setOpenId.emit(this.isOpen ? this.id : null);
		}
	}

	/**
	 * Checks if among menu items children is an active one
	 */
	private hasActiveChild(item: NavItem): boolean {
		if (item.children?.length) {
			return item.children.some((child) => this.hasActiveChild(child));
		} else {
			return !!item.isActive;
		}
	}
}
