import { Pipe, PipeTransform } from '@angular/core';

import { NavItem } from '../../models';

@Pipe({
	name: 'uiNgNavMenuFilter',
})
export class NavMenuFilterPipe implements PipeTransform {
	/**
	 * Transform function returns array of items matching search term
	 * @param value - menu item to be filtered
	 * @param term - search term
	 */
	transform(value: NavItem[] = [], term: string = '') {
		const searchText = term.trim();

		if (!searchText) {
			return value;
		}

		return value.filter((item) =>
			this.containsFilterValue(item, searchText),
		);
	}

	private containsFilterValue(
		menuItem: NavItem,
		filterValue: string,
	): boolean {
		return !menuItem.children?.length
			? menuItem.name.toLowerCase().includes(filterValue.toLowerCase())
			: menuItem.children.some((child) =>
					this.containsFilterValue(child, filterValue),
			  );
	}
}
