import { ChangeDetectionStrategy, Component, Input } from '@angular/core';

type ImgAnimation = 'zoom-in' | 'rotate-l' | 'rotate-r' | null;
type ImgOverlayAnimation =
	| 'slide-top'
	| 'slide-right'
	| 'slide-down'
	| 'slide-left'
	| 'zoom-in'
	| 'zoom-out'
	| null;
type ImgOverlayBgColor = 'black' | 'white' | 'primary-dark';

@Component({
	selector: 'ui-ng-image-overlay',
	templateUrl: './image-overlay.component.html',
	styleUrls: ['./image-overlay.component.scss'],
	changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ImageOverlayComponent {
	/**
	 * Image Name
	 */
	@Input()
	imgName = 'image';

	/**
	 * Image Source
	 */
	@Input()
	imgSrc!: string;

	/**
	 * Image Animation Name
	 */
	@Input()
	imgAnimation: ImgAnimation = null;

	/**
	 * Image Overlay Animation Name
	 */
	@Input()
	imgOverlayAnimation: ImgOverlayAnimation = null;

	/**
	 * Image Overlay Background Color
	 */
	@Input()
	imgOverlayBgColor: ImgOverlayBgColor = 'primary-dark';
}
